import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { GlobalValidate } from '../../../../../_services/validate.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatFormFieldModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import { Router, Event, NavigationStart, NavigationEnd } from '@angular/router';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import {FormBuilder,FormControl, FormGroup, Validators} from '@angular/forms';
import {ListComponent, SafeHtmlPipe} from '../../../list/list.component';
import { ActivatedRoute} from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;
//import { SmartSearchComponent } from '../../../layouts/smart-search/smart-search.component';
const NUM_REGAX=/[0-9]+$/;
@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./configure-product-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION, ROUND_ANTICLOCK_ANIMATION
  ],
})
export class ConfigureProductAddComponent implements OnInit,AfterViewInit {
	 @ViewChild(MatSort) sort: MatSort; 
	 @ViewChild(ListComponent) list: ListComponent;
	 @ViewChild(ToastComponent) toast: ToastComponent;
	listHide: boolean = true;
	addHide: boolean = false;
	animateAdd: string = 'out';
	animateList: string = 'in';
	startdatemsg: string;
	enddatemsg: string;
	bootstrapMsg: string;
	itemName: string;
	bootstrapFormValues = [];
	  
	configureProductForm: FormGroup;
	active: boolean = true;
	checked: boolean = false;
	opened: boolean = false;
    configureproductId = 0;  
	  public disctype = [];
	  public itemListData = [];
	pageValue: string ="1";
	sortField: string ="";
	effectivedateMsg=''
	sortDirection: string ="";
	length = 10;
    pageSize = 5;
	 msg = [];
	  
	pageIndex: number=1;
	configureproduct = new ConfigureProductData();
	  configureproductItemId=0;
	  itemId=0;
	public ratetype =[]; 
	public group =[];  
	Status;	
    constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _formBuilder: FormBuilder,
			   private route:ActivatedRoute, private router: Router,private globalValid: GlobalValidate) {
		this.route.queryParams.filter(params => params.itemId).subscribe(params=>{
			if(params.itemId != undefined){
			this.itemId =params.itemId;
			}
			console.log(this.itemId);
		});
		this.populateDropDownData();
		this.getOffersList();
		 
    }

    ngOnInit() {
 		this.configureProductForm = new FormGroup({		
			'itemGroupId' : new FormControl('', [ Validators.required] ),
			'itemQty' : new FormControl('', [ Validators.required] ),
			'itemSelectedActive' : new FormControl(''),
			}); 
		
				}
	  get itemGroupId(){ return this.configureProductForm.get('itemGroupId')};
	  get itemQty(){ return this.configureProductForm.get('itemQty')};
	  get itemSelectedActive(){ return this.configureProductForm.get('itemSelectedActive')};
	  
	  
	  toggleAnimation(){
		  this.router.navigate(['/Inventory/itemlist/itemconfigureproduct/'], {queryParams : { configureproductItemId : this.configureproductItemId}});

	  }
	  
	  
	  
	   populateDropDownData(){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		       this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/inventory-groups',{},
									 { headers, withCredentials: true }).subscribe(data =>{
				  														this.group = data['populateInventoryGroups']
			  															});
			  
	 }
	  
	  getCheckValues(temp:string){
	return this.globalValid.getCheckValue(temp);
	
	}
	  populateData(){
			   const headers = new HttpHeaders({'Content-Type':'application/json'});
			   this.http.post<ConfigureProductData>(sessionStorage.getItem('requestUrl')+'/axelacrm/inventory/item-configureproduct-data',
													{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.configureproduct = data['populateData'][0];
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 },
															 () => {
				   
				                                              });
	  
			  	
	 }
    ngAfterViewInit() {
		$.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
    }
	  
	  
	 getOffersList(){
			const headers = new HttpHeaders({'Content-Type':'application/json'});
			   this.http.post<ConfigureProductData>(sessionStorage.getItem('requestUrl')+'/axelacrm/inventory/item-option-data',JSON.stringify({'itemId' : this.itemId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.itemListData = data['populateConfigureProductListData'];
				   														this.itemName = data['itemName'];
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 },
															 () => {
				   
				                                              });
	  
	  }
	  
	  
	  
	  getItemList(value:string){
		    const headers = new HttpHeaders({'Content-Type':'application/json'});
			   this.http.post<ConfigureProductData>(sessionStorage.getItem('requestUrl')+'/axelacrm/inventory/item-option',JSON.stringify({'searchName' : value,'requestType' : 'onchange',itemId:this.itemId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.itemListData = data['populateOnchangeSearch'];
				   if(this.itemListData.length>0){
					   this.listHide=false;
				   }
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 },
															 () => {
				   
				                                              });
	  
	  }
	  
	  
	  
	  GetOptionType(event){
		  console.log("dalihcfpuoizxv");
		  console.log(event.vale);
	  }
	  
	  
	  onSubmit() {
		  this.bootstrapFormValues = $('#m_form_1').serializeArray();
		for(var i=0;i<this.bootstrapFormValues.length;i++){
			 if(this.bootstrapFormValues[i].name.includes('check_')){
					this.configureproduct[this.bootstrapFormValues[i].name.split('_')[1]] = "1";
				}else{
					this.configureproduct[this.bootstrapFormValues[i].name] = this.bootstrapFormValues[i].value;
				}
		}
		  
		  
		  for(var i=0;i<this.bootstrapFormValues.length;i++){
				console.log(this.bootstrapFormValues[i]);
				if(this.bootstrapFormValues[i].name=="startDate"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter Start Date!<br>';
						this.startdatemsg="<font class='errorMsg'>Enter Start Date</font>";
						
					}else{
						this.startdatemsg = "";
					}
				}
			  
			  if(this.bootstrapFormValues[i].name=="endDate"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter End Date!<br>';
						this.enddatemsg="<font class='errorMsg'>Enter End Date</font>";
						
					}else{
						this.enddatemsg = "";
					}
				}
			}
		  
	if(!this.configureProductForm.invalid ){
			   const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.configureproductId == 0){
			 this.configureproduct['requestType'] = 'add';
		}else{
			this.configureproduct['requestType'] = 'update';
			this.configureproduct['configureproductId'] = this.configureproductId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/inventory/item-configureproduct',
									JSON.stringify(this.configureproduct),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}
														}
			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
														this.toggleAnimation();
			 											this.configureProductForm.reset();
														}
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
			 
		 												});	  
			  
		
		  }
		  
	  
	  }
	  
	 
	  
}

export class ConfigureProductData {
	
itemGroupId : string;
itemQty : string;
startDate : string='';
endDate : string='';
itemSelectedActive : string='1';

}



